import React from 'react';
import { View, Text, TouchableOpacity, StyleSheet } from 'react-native';
import { useNavigation } from '@react-navigation/native';

//===========================================================================================
const Breadcrumbs = ({ links }) => {
    const navigation = useNavigation();

    const navigator = links.navigator;
    links = links.routes;

    const navigate = (_route) => {
        console.log(`${navigator} -> ${_route}`);
        navigation.navigate(navigator);
        navigation.navigate(_route);
    };

    //===========================================================================================
    return (
        <View style={styles.container}>
            {links.map((v, i) => 
                <TouchableOpacity
                    onPress={() => { navigate(v.route) }}
                >
                    <Text style={i == links.length - 1 ? styles.lastBreadcrumb : styles.breadcrumb} >
                        {i == links.length - 1 ? v.title : v.title + ' >'}
                    </Text>
                </TouchableOpacity>
            )}
        </View>
    );
};

export default Breadcrumbs;

//======================================================================================
const styles = StyleSheet.create({
    container: {
        flexDirection: 'row',
        gap: '0.25rem',
        justifyContent: 'flex-start',
        alignItems: 'center'
    },
    breadcrumb: {
        color: '#ccc',
        fontSize: '0.9rem'
    },
    lastBreadcrumb: {
        fontWeight: 'bold',
        color: '#bbb',
        fontSize: '1rem'
    }
});